<template>
  <div>
    <div class="header">
      <span v-if="this.open" @click="mounted"> &#x25B2 Collapse</span>
      <span v-else="!this.open" @click="mounted"> &#x25BC Expand</span>
    </div>
    <!-- injectible, toggable content - e.g when calling elsewhere -->
    <!-- use as follows: <CollapsibleSection><div>My Content</div></CollapsibleSection> -->
    <slot v-if="this.open">
    </slot>
  </div>
</template>

<script>
  export default {
  	name: 'CollapsibleSection',
    props: ['open'],
    mounted() {
        console.log('child1');
  	    this.$emit('click', this.open = !this.open);
        console.log('child2');
    },
  };
</script>

<style scoped>

  .header {
  	background-color: #bbb;
  	padding: 3px;
  	cursor: pointer;
  }
</style>